.menuContainer {
  padding-right: 4px; }
  .menuContainer .menuBLD {
    display: table;
    padding: 10px; }
    .menuContainer .menuBLD > div {
      display: table-cell; }
    .menuContainer .menuBLD > div:not(:last-of-type) {
      padding-right: 20px; }
    .menuContainer .menuBLD h4 {
      margin: 0 0 10px 0; }
    .menuContainer .menuBLD .menuList {
      min-width: 100px;
      line-height: 1.8;
      display: table; }
      @media screen and (orientation: portrait) {
        .menuContainer .menuBLD .menuList {
          display: flex;
          flex-direction: column; }
          .menuContainer .menuBLD .menuList > div[data-list]:not(:last-of-type) {
            padding-right: 0;
            padding-bottom: 15px; } }
      .menuContainer .menuBLD .menuList > div {
        max-width: 140px; }
      .menuContainer .menuBLD .menuList > div:not([data-list]) {
        margin-bottom: 5px; }
      .menuContainer .menuBLD .menuList > div[data-list] {
        display: table-cell; }
        .menuContainer .menuBLD .menuList > div[data-list]:not(:last-of-type) {
          padding-right: 15px; }
        .menuContainer .menuBLD .menuList > div[data-list] h6 {
          color: #ffc206;
          font-size: .8rem;
          margin: 0 0 5px 0; }
        .menuContainer .menuBLD .menuList > div[data-list] p {
          margin: 0; }
